<template>
<v-app id="inspire">
    <v-content>
        <v-container fluid fill-height>
            <v-layout align-center justify-center>
                <v-flex xs12 sm8 md4>
                    <v-card class="elevation-12">
                        <v-toolbar dark color="teal">
                            <v-toolbar-title>ROMA Contabilidade</v-toolbar-title>
                            <v-spacer></v-spacer>
                        </v-toolbar>
                        <v-card-text>
                            <v-form>
                                <v-text-field color="teal" prepend-icon="person" name="login" v-model="user.email" label="Login" type="text"></v-text-field>
                                <v-text-field color="teal" id="password" prepend-icon="lock" v-model="user.password" name="password" label="Password" type="password"></v-text-field>
                            </v-form>
                        </v-card-text>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn outline @click="goCadastro" color="teal">Cadastre-se</v-btn>
                            <v-btn @click="logar" dark color="teal">Login</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-flex>
            </v-layout>
        </v-container>
    </v-content>
</v-app>
</template>

<script>
import userService from "../services/userService";

export default {
    data: () => ({
        drawer: null,
        user: {}
    }),

    props: {
        source: String
    },
    methods: {
        logar() {
            if (this.user.email != null && this.user.password != null) {
                userService
                    .login(this.user).then(r => {
                        if(r.status == 200){
                           this.$cookie.set("user", JSON.stringify(r.data), 1);
                           this.$router.push("/");
                        }
                    });
            }
        },
        goCadastro() {
          this.$router.push("/cadastro");
        }
    }
}
</script>
