import { http } from './config'

export default {
    //Logar
    login: (usuario) => {
        return http.post("customers/autenticate", usuario)
    },
    //Cadastro de usuário
    cadastro: (id, usuario) => {
        return http.post("customers/userComum/"+id, usuario)
    },
    cadastroClient: (usuario) => {
        return http.post("customers/client", usuario)
    },
    //Listar funcionários
    listarFuncionarios:(id) => {
        return http.get('customers/funcionarios/'+id)
    },
    //Listar usuários
    listarUsuarios:() => {
        return http.get('customers/users')
    },
     //Listar gerentes
     listarGerentes:() => {
        return http.get('customers/gerentes')
    },
    //Listar arquivos
    getFiles: (id) => {
        return http.get('customers/files/'+id)
    },
    //Download file
    fileDownload: (id) => {
        return http.get('file/download/'+id)
    },
   //Buscar usuario por id
   getUser: (id) => {
       return http.get('customers/user/'+id)
   },
  
}